@import url(https://fonts.googleapis.com/css?family=Special+Elite&display=swap);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css?family=Do+Hyeon|Press+Start+2P&display=swap&subset=cyrillic-ext);
@import url(https://fonts.googleapis.com/css?family=Bungee|Do+Hyeon|Press+Start+2P&display=swap&subset=cyrillic-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color:#212121;
  cursor: pointer;
}

.lotties{
  display: grid;
  grid-template-columns: auto auto;
}

@media only screen and (min-device-width : 320px) and (max-device-width: 480px){
  .lotties{
    display: grid;
    grid-template-columns: auto;
  }
}


.user-div{
  margin-top: 20px;
  background-color: #61dafb;
}

.custom-lottie{
  height: 100px;
  width: 50%;
}

.div-container{
  background-color: #424242;
}

.custom-header-navbar{
  position: fixed;
  background-image: linear-gradient(to right, #000000, #000000, #1f1b1b);
  color: aliceblue;
  font-size: 30px;
  font-family: 'Do Hyeon', sans-serif;

}

.form-navbar{
  width: 200px;
}

