  z-index: 5;
}

.para-text{
  margin-top: 20px;
  color: #FFFFFF;
  font-family: 'Bungee', cursive;
  font-size: 50px;
  line-height: 1.1;
  z-index: 5;
  -webkit-animation: color-change 4s infinite;
          animation: color-change 4s infinite;
}

@-webkit-keyframes color-change {
  0% { color: rgb(131, 122, 122); }
  50% { color: #757575; }
  100% { color: #616161; }
}

@keyframes color-change {
  0% { color: rgb(131, 122, 122); }
  50% { color: #757575; }
  100% { color: #616161; }
}

